.logo {
  display: flex;
  justify-content: center;
}

.logo__part {
  display: flex;
  flex-direction: column;
}

.logo__part-1 {
  font-family: "Ubuntu medium", Arial, sans-serif;
  font-size: 1.875rem;
  font-weight: 500;
  color: #434343;
}

.logo__part-2 {
  font-family: "Ubuntu light", Arial, sans-serif;
  font-size: 0.75rem;
  font-weight: 300;
  color: #333;
  text-transform: uppercase;
  letter-spacing: 0.21875rem;
}

.social__list {
  display: flex;
}

.social__link {
  display: block;
  width: 1rem;
  height: 1rem;
  margin-left: 0.9375rem;
  background-position: 50% 50%;
  background-repeat: no-repeat;
}

.social__svg {
  fill: rgba(42, 45, 50, 0.2);
}

.social__svg:hover {
  fill: #248cec;
}

.main-navigation {
  display: flex;
  justify-content: space-between;
  background-color: #2a2d32;
}

.nav {
  display: flex;
}

.nav__item {
  display: flex;
  justify-content: center;
  align-items: center;
  border-left: 1px solid rgba(21, 21, 21, 0.5);
  border-right: 1px solid rgba(21, 21, 21, 0.5);
}

.nav__link {
  font-family: "Ubuntu medium", Arial, sans-serif;
  font-size: 0.875rem;
  font-weight: 400;
  color: #fff;
  padding: 1.4375rem 1.875rem;
  text-decoration: none;
  text-transform: uppercase;
}

.nav__link:active {
  background-color: #248cec;
}

.nav__link:hover {
  color: #f4b60d;
}

.search {
  position: relative;
}

.search__input {
  width: 15.0625rem;
  padding: 0.875rem;
  margin: 0.5rem;
  background-color: rgba(255, 255, 255, 0.05);
  border: 1px solid #181a1d;
  color: #fff;
  outline: none;
}

.search__btn {
  position: absolute;
  top: 35%;
  right: 8%;
  width: 1.25rem;
  height: 1.25rem;
  background-color: transparent;
  border-style: none;
  background-image: url(../img/search.svg);
  background-position: 50% 50%;
  background-repeat: no-repeat;
}

.jcarousel {
  position: relative;
  overflow: hidden;
  max-width: 100%;
  vertical-align: middle;
  margin-top: 40px;
}

.jcarousel__nav {
  width: 20000rem;
  position: relative;
  list-style: none;
  margin: 0;
  padding: 0;
}

.jcarousel__item {
  float: left;
}

.jcarousel__img {
  max-width: 100%;
}

.jcarousel__pagination {
  display: flex;
  position: absolute;
  top: 90%;
  right: 50%;
}

.jcarousel__controls {
  width: 0.8125rem;
  height: 0.8125rem;
  border: 0.125rem solid #fff;
  margin-right: 0.9375rem;
  background-color: #fff;
  text-indent: -9999px;
}

.jcarousel__controls:last-child {
  margin-right: 0;
}

.jcarousel__controls--active {
  background-color: #2a2d32;
}

.slider {
  position: relative;
}

.slider__header {
  font-family: "Ubuntu medium", Arial, sans-serif;
  font-size: 3.375rem;
  font-weight: 500;
  color: #fff;
  position: absolute;
  width: 15rem;
  bottom: 5rem;
  left: 2.1875rem;
  text-transform: uppercase;
  z-index: 10;
}

.slider__header-span {
  position: relative;
  color: #fff;
  padding: 1.5625rem;
}

.slider__header-span--1 {
  color: #f4b60d;
  background-color: rgba(42, 45, 50, 0.9);
}

.slider__header-span--3 {
  background-color: rgba(42, 45, 50, 0.9);
  z-index: -1;
}

.advantages {
  display: flex;
}

.advantages__item {
  padding: 4.375rem 2.5rem 5rem 0;
}

.advantages__item:last-child {
  padding-right: 0;
}

.advantage {
  display: flex;
  align-items: baseline;
}

.advantage__number {
  font-family: "Ubuntu medium", Arial, sans-serif;
  font-size: 5.625rem;
  font-weight: 500;
  color: rgba(42, 45, 50, 0.1);
  margin-right: 2.1875rem;
}

.advantage__icon {
  display: block;
  width: 2rem;
  height: 2rem;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.advantage__icon--settings {
  background-image: url(../img/settings.svg);
}

.advantage__icon--tech {
  background-image: url(../img/params.svg);
}

.advantage__icon--support {
  background-image: url(../img/truck.svg);
}

.advantage__header {
  font-family: "Ubuntu medium", Arial, sans-serif;
  font-size: 1.25rem;
  font-weight: 500;
  color: #2a2d32;
  margin-top: 1.1875rem;
  margin-bottom: 1.75rem;
}

.advantage__text {
  font-family: Arial, sans-serif;
  font-size: 0.8125rem;
  font-weight: 400;
  color: #8a8a8a;
  height: 3.75rem;
  margin-bottom: 1.5625rem;
  line-height: 1.25rem;
  overflow: hidden;
}

.advantage__read-next {
  font-family: "Ubuntu medium", Arial, sans-serif;
  font-size: 0.8125rem;
  font-weight: 500;
  color: #2a2d32;
  padding-right: 1.25rem;
  text-decoration: none;
  background: url(../img/arrow.svg) 100% 50% no-repeat;
}

.advantage__read-next:hover {
  color: #248cec;
}

.services {
  padding: 2.8125rem 0;
  background-color: #2a2d32;
}

.services__header {
  font-family: "Ubuntu medium", Arial, sans-serif;
  font-size: 1.5rem;
  font-weight: 500;
  color: #fff;
  margin: 0 auto;
}

.services__links {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.services__item {
  padding: 2.3125rem 1.125rem;
  text-align: center;
}

.services__item:first-child {
  padding-left: 0;
}

.services__item:last-child {
  padding-right: 0;
}

.service__wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.service__wrapper:before {
  display: none;
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: rgba(36, 140, 236, 0.7);
}

.service__wrapper:after {
  display: none;
  content: '+';
  position: absolute;
  color: #fff;
  font-size: 3.125rem;
  z-index: 100;
}

.service__image {
  width: 100%;
}

.service__link {
  text-decoration: none;
  position: relative;
}

.service__link:hover .service__header {
  color: #f4b60d;
}

.service__link:hover .service__wrapper:before,
.service__link:hover .service__wrapper:after {
  display: block;
}

.service__header {
  font-family: "Ubuntu medium", Arial, sans-serif;
  font-size: 1rem;
  font-weight: 500;
  color: #fff;
  margin-top: 1.875rem;
}

.news-adv {
  background-color: #f7f7f7;
}

.news-adv__wrapper {
  display: flex;
  justify-content: space-between;
  padding: 3.9375rem 0 5.125rem 0;
}

.news-adv__item {
  flex: 1 1 0;
}

.news__header, .banners__header {
  font-family: "Ubuntu medium", Arial, sans-serif;
  font-size: 1.5rem;
  font-weight: 500;
  color: #2a2d32;
  margin-bottom: 3rem;
}

.article {
  display: flex;
  align-items: flex-start;
}

.article:first-child {
  padding-bottom: 1.875rem;
}

.article__picture {
  position: relative;
}

.article__date {
  display: flex;
  flex-flow: column;
  position: absolute;
  top: 0.625rem;
  left: -1.125rem;
}

.article__month, .article__day {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 2.5rem;
  height: 2.5rem;
}

.article__month {
  font-family: "Ubuntu medium", Arial, sans-serif;
  font-size: 0.9375rem;
  font-weight: 500;
  color: #fff;
  background-color: #248cec;
}

.article__day {
  font-family: "Ubuntu medium", Arial, sans-serif;
  font-size: 1.125rem;
  font-weight: 500;
  color: #2a2d32;
  background-color: #f4b60d;
}

.article__content {
  padding: 0.3125rem 2.1875rem;
  overflow: hidden;
}

.article__header {
  font-family: "Ubuntu medium", Arial, sans-serif;
  font-size: 1rem;
  font-weight: 500;
  color: #2a2d32;
  display: block;
  margin-bottom: 0.625rem;
  cursor: pointer;
}

.article__header:hover {
  color: #248cec;
}

.article__info {
  font-family: Arial, sans-serif;
  font-size: 0.8125rem;
  font-weight: 400;
  color: #8a8a8a;
  margin-bottom: 1.5625rem;
}

.article__info-link {
  color: #248cec;
  text-decoration: none;
}

.article__text, .accordion__text {
  font-family: Arial, sans-serif;
  font-size: 0.8125rem;
  font-weight: 400;
  color: #8a8a8a;
  max-height: 5rem;
  line-height: 1.25rem;
  overflow: hidden;
}

.accordion__panel {
  margin-bottom: 0.3125rem;
}

.accordion__panel:last-child {
  margin-bottom: 0;
}

.accordion__header {
  font-family: "Ubuntu medium", Arial, sans-serif;
  font-size: 0.8125rem;
  font-weight: 500;
  color: #2a2d32;
  display: flex;
  text-transform: uppercase;
  background-color: #fff;
  border: 0.0625rem solid #e8e8e8;
  cursor: pointer;
}

.accordion__header--active {
  color: #fff;
  background-color: #f4b60d;
}

.accordion__header-icon {
  width: 0.625rem;
  padding: 0.8125rem 0.9375rem;
  padding-top: 0.6875rem;
  border-right: 0.0625rem solid #e8e8e8;
  font-size: 0.9375rem;
  text-align: center;
}

.accordion__header-caption {
  padding: 0.8125rem;
}

.accordion__content {
  display: none;
  padding: 1.875rem 2.3125rem;
  max-height: 5.9375rem;
}

.accordion__content--visible {
  display: block;
}

.logos__list {
  display: flex;
  justify-content: space-between;
  padding: 5.625rem 2.5rem;
}

.footer {
  background-color: #2a2d32;
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;
}

.footer__links {
  padding: 3.75rem 0 5.625rem 0;
}

.footer-nav {
  flex: 1 1 0;
  padding-right: 2.5rem;
}

.footer-nav:last-child {
  padding-right: 0;
}

.footer-nav__header {
  font-family: "Ubuntu medium", Arial, sans-serif;
  font-size: 1rem;
  font-weight: 500;
  color: #fff;
  padding-left: 0.3125rem;
  line-height: 1.5rem;
  text-transform: uppercase;
  margin-bottom: 3rem;
}

.footer-nav__item {
  display: flex;
  align-items: center;
  border-bottom: 0.0625rem solid rgba(255, 255, 255, 0.07);
}

.footer-nav__item:last-child {
  border-bottom: none;
}

.footer-nav__link {
  font-family: Arial, sans-serif;
  font-size: 0.8125rem;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.3);
  padding: 0.625rem 0.3125rem;
  text-decoration: none;
}

.footer-nav__link:hover {
  color: #248cec;
}

.footer__contacts {
  background-color: rgba(255, 255, 255, 0.05);
}

.footer__contacts-item {
  padding: 1.25rem;
  font-family: Arial, sans-serif;
  font-size: 0.75rem;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.2);
  background-repeat: no-repeat;
  background-position: 0 50%;
  text-decoration: none;
}

.footer__address {
  background-image: url(../img/location.svg);
}

.footer__tel {
  background-image: url(../img/phone.svg);
}

.footer__email {
  background-image: url(../img/email.svg);
}

.footer__copyright {
  margin-left: auto;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2.1875rem 0;
}

.wrapper {
  max-width: 72.5rem;
  margin: 0 auto;
}
